main {
  display: grid;
  grid-column: 1/-1;
  grid-template-columns: 1fr;
  justify-items: center;
  max-width: 500px;
  width: 100%;
  font-family: "asap";
}
main #Icon {
  width: 18%;
  margin: 20px 0 26px 0;
}
main #WelcomeMessage {
  display: grid;
  width: 100%;
  grid-column: 1/-1;
  grid-template-columns: 1fr 4fr;
}
main #WelcomeMessage h3 {
  grid-column: 2/3;
  grid-row: 1;
  align-self: center;
  margin-left: 5%;
  font-size: 25px;
}
main #WelcomeMessage img {
  width: 65%;
  justify-self: end;
  grid-column: 1/2;
  grid-row: 1;
}
main #NewestTrips {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 22px;
  flex-wrap: wrap;
}
main #NewestTrips h3 {
  font-size: 29px;
  margin-bottom: 11px;
}
main #NewestTrips .TripsHolder {
  width: 90%;
  display: grid;
  grid-template-columns: 1.1fr 2fr 0.7fr;
  grid-column: 1/-1;
  grid-template-rows: 1fr fr;
  color: #FDFDFD;
  font-family: "asap";
  margin: 0 0 12px 0;
}
main #NewestTrips .TripsHolder .Date {
  grid-column: 1/2;
  background-color: #005140;
  grid-row: 1/3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border-radius: 0.25em 0 0 0.25em;
  padding: 5% 0 5% 0;
}
main #NewestTrips .TripsHolder .Date .Month {
  font-size: 20px;
  font-weight: normal;
}
main #NewestTrips .TripsHolder .Date .Number {
  font-size: 40px;
  font-weight: bold;
}
main #NewestTrips .TripsHolder .Info {
  grid-column: 2/3;
  background-color: #EAEAEA;
  grid-row: 1/3;
  color: #000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5% 0 5% 5%;
  font-size: 14px;
  font-weight: normal;
}
main #NewestTrips .TripsHolder .Time {
  grid-column: 3/4;
  background-color: #00904A;
  grid-row: 1/2;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 0 0.25em 0 0;
  font-size: 14px;
  font-weight: normal;
}
main #NewestTrips .TripsHolder .Country {
  grid-column: 3/4;
  background-color: #313131;
  grid-row: 2/3;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 0 0 0.25em 0;
  font-size: 14px;
  font-weight: normal;
}
